import React, {useRef} from "react";
import Button from "../Button/Button"
import telegram_icon from "../../Components/Hat/images/telegram-icon.svg"
import "./ServiceInfo.sass"
import {IAnimated, useJustAnimation} from "../Animation/Animation";
import {useTagsList} from "../Tag/Tag";
import MediaFrame from "../MediaFrame/MediaFrame";
import Preloader from "../Preloader/Preloader";

interface IServiceInfoProps {
    iconSrc: string,
    iconAlt?: string,
    title: string,
    children: React.ReactNode,
    className?: string,
    style?: React.CSSProperties
}

const ServiceInfo: React.FC<IServiceInfoProps & IAnimated> =
    ({
         animationOrder,
         children,
         className,
         style,
         iconSrc,
         title,
         iconAlt
     }) => {

        //animation
        const tagsList = useTagsList()
        const containerRef = useRef<HTMLElement>(null)
        const justAnimationStyles = useJustAnimation({
            doStartAnimation: true,
            objectRef: containerRef,
            animationOrder,
            deps: [tagsList]
        })

        return <section ref={containerRef} className={["service-info", className].join(" ")}
                        style={{...style, ...justAnimationStyles}}>
            <MediaFrame src={iconSrc} alt={iconAlt} className="service-info__icon" preloader={<Preloader/>}/>
            <h2 className="service-info__title">{title}</h2>
            <div className="service-info__content">{children}</div>
            <Button color="var(--accent)" className="service-info__button" leftIconSrc={telegram_icon}>Узнать подробнее
                в
                ТГ</Button>
        </section>
    }

export default ServiceInfo