import Hat from "../../Components/Hat/Hat"
import "./HomePage.sass"
import Button from "../../Components/Button/Button"
import Case from "../../Components/Case/Case"
import Footer from "../../Components/Footer/Footer"
import ThreeD from "../../Components/Case/script/ThreeD";
import {useEffect, useRef, useState} from "react";

import telegram_icon from "./images/telegram-icon.svg"
import react_icon from "../../DataBase/images/react.png"
import laravel_icon from "../../DataBase/images/laravel.png"
import {CASES, CaseType} from "../../DataBase/cases";
import useHorizontalScroll from "../../Hooks/useHorizontalScroll";
import image_for_mobile from "./images/image-for-mobile.png"
import ServiceInfo from "../../Components/ServiceInfo/ServiceInfo";
import Tag, {TagsSet, useSelectedTagsList} from "../../Components/Tag/Tag";
import {SERVICES, ServiceType} from "../../DataBase/services";
import Animation from "../../Components/Animation/Animation";
import MediaFrame from "../../Components/MediaFrame/MediaFrame";
import video from "./images/video.mp4"
import image from "./images/image.png"
import Preloader from "../../Components/Preloader/Preloader";
import {GLOBAL_PIXEL_SIZE} from "../../index";

const HomePage = () => {
    const canvasRef = useRef<any>()
    const threeDRef = useRef<ThreeD>(new ThreeD())
    const homePageRef = useRef<HTMLDivElement>(null)
    const selectedTagsList = useSelectedTagsList()
    const isScrollVisible = useHorizontalScroll(homePageRef)

    //cases
    const [casesToShow, setCasesToShow] = useState<Array<CaseType>>([])

    //services
    const [servicesToShow, setServicesToShow] = useState<Array<ServiceType>>([])

    //starting threeD
    useEffect(() => {

        //start ThreeD
        threeDRef.current.start(canvasRef.current)

        if (homePageRef.current)
            homePageRef.current.addEventListener("scroll", () => {
                threeDRef.current.render()
            })
    }, [])

    //filter cases from tags
    useEffect(() => {
        const casesListFromTags = CASES.filter(_case => {
            let hasOneTag = false

            if (_case.tags && selectedTagsList) {
                _case.tags.forEach(elem => {
                    selectedTagsList.forEach(tag => {
                        if (elem === tag)
                            hasOneTag = true
                    })
                })
            }
            return hasOneTag
        })

        if (selectedTagsList?.length !== 0) {
            setCasesToShow(casesListFromTags)
        } else {
            setCasesToShow(CASES)
        }
    }, [selectedTagsList])

    //filter services from tags
    useEffect(() => {
        const servicesListFromTags = SERVICES.filter(services => {
            let hasOneTag = false

            if (services.tags && selectedTagsList) {
                services.tags.forEach(elem => {
                    selectedTagsList.forEach(tag => {
                        if (elem === tag)
                            hasOneTag = true
                    })
                })
            }
            return hasOneTag
        })

        setServicesToShow(servicesListFromTags)
    }, [selectedTagsList])

    //reset scroll when tags has changed
    useEffect(() => {
        if (homePageRef.current)
            homePageRef.current.scrollLeft = 0
    }, [selectedTagsList])

    return <>
        <canvas ref={canvasRef} style={{
            position: "fixed",
            width: "100%",
            height: "100%",
            top: "0",
            left: "0",
            zIndex: "-1"
        }}/>
        <Hat/>
        <main ref={homePageRef}
              className={["home-page", isScrollVisible ? "home-page_scroll-visible" : null, !selectedTagsList?.length ? "home-page_has-hello" : null].join(" ")}>
            <div className="home-page__wrapper">
                <section className="home-page__hello">
                    <h1 className="home-page__hello-title">
                        <span className="home-page__hello-title-desktop">
                            <Animation deps={[!selectedTagsList?.length]} delay={3}>Создаю крутые сайты</Animation><br/>
                            <Animation deps={[!selectedTagsList?.length]} delay={4}>и дизайню всякое</Animation>
                        </span>
                        <span className="home-page__hello-title-mobile">
                            <Animation deps={[!selectedTagsList?.length]} delay={1}>Прогаю сайты</Animation><br/>
                                    <Animation deps={[!selectedTagsList?.length]} delay={3}>и дизайню</Animation><br/>
                                    <Animation deps={[!selectedTagsList?.length]} delay={5}>всякое</Animation>
                        </span>
                    </h1>
                    <MediaFrame deps={[!selectedTagsList?.length]}
                                src={image_for_mobile}
                                className="home-page__hello-image-for-mobile"/>
                    <p className="home-page__hello-sub-title">
                        <Animation deps={[!selectedTagsList?.length]}>Привет меня зовут Артем, и я занимаюсь front-end
                            разработкой на react и дизайном.</Animation>
                    </p>
                    <Button deps={[!selectedTagsList?.length]} doStartAnimation animationOrder={2} color="var(--gary-1)"
                            size="big"
                            className="home-page__hello-button"
                            leftIconSrc={telegram_icon}>
                        Связаться со мной в ТГ
                    </Button>

                    <MediaFrame
                        deps={[!selectedTagsList?.length]}
                        width={1275}
                        height={1179}
                        animationParams={{
                            scale: [1, 1]
                        }}
                        doStartAnimation
                        animationOrder={3}
                        className="home-page__hello-background"
                        src={image}
                        videoSrc={video}/>

                    <MediaFrame
                        deps={[!selectedTagsList?.length]}
                        width={1275}
                        height={1179}
                        animationParams={{
                            scale: [1, 1]
                        }}
                        doStartAnimation
                        animationOrder={3}
                        className="home-page__hello-background-for-tablet"
                        src={image}
                        videoSrc={video}/>
                </section>
                <section className="home-page__tags-place">
                    <div className="home-page__tags-place-title">
                        Кейсы и услуги
                    </div>
                    <div className="home-page__tags-place-list">
                        <TagsSet size="medium"/>
                    </div>
                </section>
                {casesToShow.length > 0 ? <section className="home-page__cases">
                    {casesToShow.map((elem, index) => {
                        return index < 1 ?(
                            <Case
                                key={elem.id}
                                previewImage={elem.srcToPreviewImage}
                                threeD={threeDRef.current}
                                typeOfThreeDCase={elem.typeOfThreeDCase}
                                className="home-page__case"
                                title={elem.title}
                                description={elem.description}
                                links={elem.links.map(link => {
                                    return {
                                        iconSrc: link.srcToIcon,
                                        link: link.link,
                                        text: link.label
                                    }
                                })}
                            />
                        ) : null
                    })}
                </section> : null}
                {
                    servicesToShow.length !== 0
                        ?
                        <div className="home-page__services">
                            {servicesToShow.map((service, index) => {
                                return <ServiceInfo
                                    animationOrder={index}
                                    key={service.id}
                                    iconSrc={service.iconSrc}
                                    title={service.name}
                                    className="home-page__service"
                                >
                                    {service.content}
                                </ServiceInfo>
                            })}
                        </div>
                        :
                        null
                }
                {casesToShow.length > 0 ? <section className="home-page__cases">
                    {casesToShow.map((elem, index) => {
                        return index > 1 ? (
                            <Case
                                key={elem.id}
                                previewImage={elem.srcToPreviewImage}
                                threeD={threeDRef.current}
                                typeOfThreeDCase={elem.typeOfThreeDCase}
                                className="home-page__case"
                                title={elem.title}
                                description={elem.description}
                                links={elem.links.map(link => {
                                    return {
                                        iconSrc: link.srcToIcon,
                                        link: link.link,
                                        text: link.label
                                    }
                                })}
                            />
                        ) : null
                    })}
                </section> : null}
                {casesToShow.length === 0 ? <section className="home-page__empty">
                    <h2 className="home-page__empty-title">
                        <div className="home-page__empty-title-desktop">
                            <Animation deps={[selectedTagsList]}>Таких работ</Animation><br/>
                            <Animation delay={1} deps={[selectedTagsList]}>нету в портфолио :(</Animation>
                        </div>
                        <div className="home-page__empty-title-mobile">
                            <Animation deps={[selectedTagsList]}>В портфолио</Animation><br/>
                            <Animation deps={[selectedTagsList]} delay={1}>нету работ</Animation>
                        </div>
                    </h2>
                    <p className="home-page__empty-text">
                        <Animation deps={[selectedTagsList]} delay={2}>
                            Будде первым, кто закажет работу в рамках этой услуги и получите скидку 60% ✨
                        </Animation></p>
                    <Button deps={[selectedTagsList]} delay={3} doStartAnimation color="var(--gary-1)" size="big"
                            leftIconSrc={telegram_icon} className="home-page__empty-button">Связаться со мной в
                        ТГ</Button>
                    <MediaFrame src={image}
                                videoSrc={video} className="home-page__empty-image"/>
                </section> : null}
                {
                    servicesToShow.length !== 0 && casesToShow.length >= 3
                        ?
                        <div className="home-page__services">
                            {servicesToShow.map((service, index) => {
                                return <ServiceInfo
                                    animationOrder={index}
                                    key={service.id}
                                    iconSrc={service.iconSrc}
                                    title={service.name}
                                    className="home-page__service"
                                >
                                    {service.content}
                                </ServiceInfo>
                            })}
                        </div>
                        :
                        null
                }
            </div>
        </main>
        <Footer className="footer_in-home-page"/>
    </>
}

export default HomePage