import "./Case.sass"
import React, {useEffect, useRef, useState} from "react";
import Button from "../Button/Button"
import link_icon from "../../Components/Case/images/link-icon.svg"
import telegram_icon from "../../Components/Case/images/telegram-icon.svg"
import ThreeD from "./script/ThreeD";
import Preloader from "../Preloader/Preloader";
import Animation, {IAnimated, useJustAnimation} from "../Animation/Animation";
import {useTagsList} from "../Tag/Tag";

interface ICaseProps {
    title: string,
    description: string,
    links: Array<{
        iconSrc?: string,
        link: string,
        text: string
    }>,
    className?: string
    threeD?: ThreeD
    typeOfThreeDCase?: string
    previewImage: string
}

const Case: React.FC<ICaseProps & IAnimated> =
    ({
         animationOrder,
         title,
         description,
         links,
         className,
         threeD,
         typeOfThreeDCase,
         previewImage
     }) => {
        const imageRef = useRef<any>()
        const [isImageDownloaded, setIsImageDownloaded] = useState<boolean>(false)
        const [isThreeDCaseDownloaded, setIsThreeDCaseDownloaded] = useState<boolean>(false)

        //animation
        const tagsList = useTagsList()
        const containerRef = useRef<HTMLElement>(null)
        const justAnimationStyles = useJustAnimation({
            doStartAnimation: true,
            objectRef: containerRef,
            animationOrder,
            deps: [tagsList]
        })

        useEffect(() => {
            const loadedImageHandle = () => {
                setIsImageDownloaded(true)
            }

            if (imageRef.current) {
                imageRef.current.addEventListener("load", loadedImageHandle)
            }

            return () => {
                if (imageRef.current) {
                    imageRef.current.removeEventListener("load", loadedImageHandle)
                }
            }
        }, [])

        useEffect(() => {

            const test = () => {
                setIsThreeDCaseDownloaded(true)
            }

            if (typeOfThreeDCase) {
                setIsThreeDCaseDownloaded(ThreeD.checkDownloadedThreeDCasesList(typeOfThreeDCase))
                ThreeD.addListenerDownloadedThreeDCasesList(test, typeOfThreeDCase)
            }

            if (threeD && typeOfThreeDCase && imageRef.current) {
                threeD.addThreeDCase(typeOfThreeDCase, imageRef.current)
            }

            return () => {
                if (threeD && typeOfThreeDCase)
                    threeD.removeThreeDCase(typeOfThreeDCase)

                if (typeOfThreeDCase)
                    ThreeD.removeListenerDownloadedThreeDCasesList(test, typeOfThreeDCase)
            }
        }, [threeD, typeOfThreeDCase])

        return <article ref={containerRef} style={justAnimationStyles} className={[
            "case",
            className,
            isImageDownloaded ? "case_downloaded-image" : null,
            isThreeDCaseDownloaded ? "case_downloaded-threeDCase" : null
        ].join(" ")}>
            <Preloader className="case__preloader"/>
            <img ref={imageRef} className="case__image"
                 src={previewImage}
                 alt=""/>
            <div className="case__text-slice">
                <h2 className="case__title">
                    {title}
                </h2>
                <p className="case__description">
                    {description}
                </p>
                <div className="case__buttons">
                    {/*<Button size="small" color="var(--gary-1)" leftIconSrc={telegram_icon}>Об услуге</Button>*/}
                    {links.map(({iconSrc, link, text}, index) => {
                        return <Button size="small" color="var(--gary-1)" key={index}
                                       leftIconSrc={iconSrc}>{text}</Button>
                    })}
                    <Button size="small" color="var(--gary-1)" className="case__link-button" leftIconSrc={link_icon}/>
                </div>
            </div>
        </article>
    }

export default Case