import React, {useEffect, useRef, useState} from "react";
import {IAnimated, useJustAnimation} from "../Animation/Animation";
import "./MediaFrame.sass"
import Preloader from "../Preloader/Preloader";

interface IMediaFrameProps {
    src: string,
    alt?: string,
    style?: React.CSSProperties,
    className?: string,
    videoSrc?: string,
    width?: number,
    height?: number,
    preloader?: React.ReactNode
}

const MediaFrame: React.FC<IMediaFrameProps & IAnimated> =
    ({
         alt,
         src,
         videoSrc,
         doStartAnimation,
         animationParams,
         animationOrder,
         style,
         className,
         width,
         height,
         preloader
     }) => {
        const [isVideoDownloaded, setIsVideoDownloaded] = useState<boolean>(false)
        const [isImageDownloaded, setIsImageDownloaded] = useState<boolean>(false)
        const imgRef = useRef<HTMLImageElement>(null)
        const videoRef = useRef<HTMLVideoElement>(null)

        //image
        useEffect(() => {
            if (imgRef.current) {
                if(imgRef.current.complete){
                    setIsImageDownloaded(true)
                }else{
                    const listener = () => {
                        setIsImageDownloaded(true)
                    }

                    imgRef.current.addEventListener("load", listener)

                    return () => {
                        if (imgRef.current)
                            imgRef.current.removeEventListener("load", listener)
                    }
                }
            }

        }, [imgRef.current])

        //video
        useEffect(() => {
            if (videoRef.current) {
                const listener = () => {
                    setIsVideoDownloaded(true)
                }

                videoRef.current.addEventListener("canplaythrough", listener)

                return () => {
                    if (videoRef.current)
                        videoRef.current.removeEventListener("canplaythrough", listener)
                }
            }
        }, [videoRef.current])

        //animation
        const containerRef = useRef<any>(null)
        const justAnimationStyles = useJustAnimation({
            doStartAnimation,
            animationOrder,
            objectRef: containerRef,
            animationParams
        })

        return <div style={{...style, ...justAnimationStyles}}
                    ref={containerRef}
                    className={[
                        "media-frame",
                        className,
                        videoSrc ? "media-frame_video" : null,
                        isImageDownloaded ? "media-frame_image-downloaded" : null,
                        isVideoDownloaded ? "media-frame_video-downloaded" : null,
                    ].join(" ")}>
            <img width={width} height={height} ref={imgRef} className="media-frame__image" src={src} alt={alt}/>
            <div className="media-frame__preloader-place">
                {!isImageDownloaded && !isVideoDownloaded ? preloader : null}
            </div>
            {
                videoSrc
                    ?
                    <video width={width} height={height} ref={videoRef} className="media-frame__video" loop autoPlay
                           muted
                           src={videoSrc}/>
                    :
                    null
            }
        </div>
    }

export default MediaFrame