import "./Footer.sass"
import React from "react";

interface IFooterProps {
    className?: string
}

const Footer: React.FC<IFooterProps> = ({className}) => {
    return <footer className={["footer", className].join(" ")}>
        <p className="footer__copyright">Артём все права засечены :3</p>
        <p className="footer__message">Все представленные здесь работы публикуются только с согласия заказчика</p>
    </footer>
}

export default Footer