import Button from "../Button/Button"
import Tag, {TagsSet, useTagsList} from "../Tag/Tag"
import {Swiper, SwiperSlide} from "swiper/react"

import 'swiper/scss'
import './Hat.sass'

import logo from "./images/logo.png"
import telegram from "./images/telegram-icon.svg"
import icon_1 from "./images/icon-1.png"
import icon_2 from "./images/icon-2.png"
import icon_3 from "./images/icon-3.png"
import arrow_left from "./images/arrow-left-icon.svg"
import arrow_right from "./images/arrow-right-icon.svg"
import React, {createContext, useCallback, useContext, useEffect, useRef, useState} from "react";
import {SwiperRef} from "swiper/swiper-react";
import Animation from "../Animation/Animation";

type TagsListItem = { label: string, isTurnedOn: boolean }

interface IHatProps {
}

const Hat: React.FC<IHatProps> = () => {

    //tags menu
    const tagsSwiperRef = useRef<SwiperRef>(null)
    const tagsSwiperNavigationButtonPrev = useRef<HTMLButtonElement>()
    const tagsSwiperNavigationButtonNext = useRef<HTMLButtonElement>()
    const [tagsSwiperPositionState, setTagsSwiperPositionState] = useState<"atStart" | "atProcess" | "atEnd">("atStart")
    const tagsList = useTagsList()

    //mobile menu
    const [isMobileMenuOpened, setIsMobileMenuOpened] = useState<boolean>(false)

    //starting
    useEffect(() => {

        //initialize scroll for tags
        if (tagsSwiperRef.current) {
            tagsSwiperRef.current.swiper.on("reachBeginning", (swiper: any) => {
                setTagsSwiperPositionState("atStart")
            })
            tagsSwiperRef.current.swiper.on("slideChange", (swiper: any) => {
                if (!swiper.isEnd && !swiper.isBeginning)
                    setTagsSwiperPositionState("atProcess")
            })
            tagsSwiperRef.current.swiper.on("reachEnd", (swiper: any) => {
                setTagsSwiperPositionState("atEnd")
            })
        }
    }, [tagsSwiperRef])

    const tagsHandlerNext = useCallback(() => {
        if (tagsSwiperRef.current)
            tagsSwiperRef.current.swiper.slideNext()
    }, [tagsSwiperNavigationButtonPrev, tagsSwiperNavigationButtonNext, tagsSwiperRef])
    const tagsHandlerPrev = useCallback(() => {
        if (tagsSwiperRef.current)
            tagsSwiperRef.current.swiper.slidePrev()
    }, [tagsSwiperNavigationButtonPrev, tagsSwiperNavigationButtonNext, tagsSwiperRef])

    return <header className={["hat", isMobileMenuOpened ? "hat_mobile-menu-is-opened" : null].join(" ")}>
        <div className="hat__logo">
            <img src={logo} alt="logotype" className="hat__logo-image"/>
        </div>
        <div className="hat__menu">
            <div className="hat__menu-some-buttons">
                <Button
                    doStartAnimation
                    animationOrder={0}
                    size="small" color="var(--white)"
                    textColor="var(--background)"
                    animationParams={{
                        translateY: ["100%", "0%"],
                        scale: [1, 1]
                    }}
                >Наши кейсы</Button>
                <Button
                    doStartAnimation
                    animationOrder={1}
                    size="small"
                    animationParams={{
                        translateY: ["100%", "0%"],
                        scale: [1, 1]
                    }}
                >Услуги</Button>
            </div>
            <button className="hat__menu-one-button" onClick={() => {
                setIsMobileMenuOpened(old => !old)
            }}>
                {/*<span className="hat__menu-one-button-notification-point"/>*/}
                <span className="hat__menu-one-button-lines-in-close-state"/>
            </button>
        </div>
        <div className="hat__separator"/>
        <Swiper ref={tagsSwiperRef}
                className={[
                    "hat__cases-tags-swiper",
                    tagsSwiperPositionState === "atStart" ? "hat__cases-tags-swiper_at-start" : null,
                    tagsSwiperPositionState === "atEnd" ? "hat__cases-tags-swiper_at-end" : null,
                ].join(" ")}
                slidesPerView="auto"
                focusableElements="input"
                watchSlidesProgress
        >

            <Button slot="container-start"
                    className="hat__cases-tags-swiper-navigation-button hat__cases-tags-swiper-navigation-button_prev"
                    leftIconSrc={arrow_left}
                    size="small"
                    onClick={tagsHandlerPrev}
                    color="var(--white)"
            />

            <Button slot="container-end"
                    className="hat__cases-tags-swiper-navigation-button hat__cases-tags-swiper-navigation-button_next"
                    leftIconSrc={arrow_right}
                    size="small"
                    onClick={tagsHandlerNext}
                    color="var(--white)"
            />
            {tagsList ? tagsList.map((tag, index) => <SwiperSlide className="hat__cases-tag-slide">
                <Tag animationOrder={index + 2}
                     doStartAnimation
                     className="hat__cases-tag"
                     animationParams={{
                         translateY: ["100%", "0%"],
                         scale: [1, 1]
                     }}
                >{tag}</Tag>
            </SwiperSlide>) : null}
        </Swiper>
        <div className="hat__social-network">
            <Button
                animationParams={{
                    translateY: ["100%", "0%"],
                    scale: [1, 1]
                }} animationOrder={10} doStartAnimation color="var(--gary-1)"
                className="hat__social-button hat__telegram-button" leftIconSrc={telegram}>
                Связаться с нами в ТГ</Button>
            <Button
                animationParams={{
                    translateY: ["100%", "0%"],
                    scale: [1, 1]
                }} animationOrder={11} doStartAnimation color="var(--gary-1)" className="hat__social-button"
                leftIconSrc={icon_1}/>
            <Button
                animationParams={{
                    translateY: ["100%", "0%"],
                    scale: [1, 1]
                }} animationOrder={12} doStartAnimation color="var(--gary-1)" className="hat__social-button"
                leftIconSrc={icon_2}/>
            <Button
                animationParams={{
                    translateY: ["100%", "0%"],
                    scale: [1, 1]
                }} animationOrder={13} doStartAnimation color="var(--gary-1)" className="hat__social-button"
                leftIconSrc={icon_3}/>
        </div>
        <div className="hat__mobile-menu">
            <a href="#" className="hat__mobile-menu-button">
                <Animation delay={1}
                           deps={[isMobileMenuOpened]}>Главная</Animation></a>
            <div className="hat__mobile-menu-tags">
                {tagsList ? tagsList.map((tag, index) =>
                    <Tag animationOrder={index + 2}
                         doStartAnimation
                         deps={[isMobileMenuOpened]}
                    >{tag}</Tag>) : null}
            </div>
            <a href="#" className="hat__mobile-menu-button">
                <Animation delay={3}
                           deps={[isMobileMenuOpened]}>Услуги</Animation></a>
            <a href="#" className="hat__mobile-menu-button">
                <Animation delay={5} deps={[isMobileMenuOpened]}>О
                    нас</Animation></a>
        </div>
    </header>
}

export default Hat