import * as THREE from "three"
import {OBJLoader} from "three/examples/jsm/loaders/OBJLoader"
import {ShaderMaterial, TextureLoader} from "three"

import centerObj from "./myCaseRecorses/center.obj"
import centerTexture from "./myCaseRecorses/center-texture.png"

import leftTopObj from "./myCaseRecorses/left-top.obj"
import leftTopTexture from "./myCaseRecorses/left-top-texture.png"

import leftBottomObj from "./myCaseRecorses/left-bottom.obj"
import leftBottomTexture from "./myCaseRecorses/left-bottom-texture.png"

import rightTopObj from "./myCaseRecorses/right-top.obj"
import rightTopTexture from "./myCaseRecorses/right-top-texture.png"

import rightBottomObj from "./myCaseRecorses/right-bottom.obj"
import rightBottomTexture from "./myCaseRecorses/right-bottom-texture.png"
import {EffectComposer} from "three/examples/jsm/postprocessing/EffectComposer";
import {GlitchPass} from "three/examples/jsm/postprocessing/GlitchPass";
import {RenderPass} from "three/examples/jsm/postprocessing/RenderPass";
import {ShaderPass} from "three/examples/jsm/postprocessing/ShaderPass";
import {GLOBAL_PIXEL_SIZE} from "../../../index";

const GLOBAL_PX_SIZE = GLOBAL_PIXEL_SIZE

export abstract class ThreeDCase {

    private isDownloaded: boolean = false

    public mainGroup: THREE.Group = new THREE.Group()
    protected centerGroup: THREE.Group = new THREE.Group()

    private _resizingHTMLElement: HTMLElement | undefined

    protected constructor(resizingHTMLElement: HTMLElement) {
        this._resizingHTMLElement = resizingHTMLElement

        //group initializing
        this.mainGroup.add(this.centerGroup)

        //initialize start method
        this.loadRes().then(() => {
            this.start()
            this.onDownloaded()
            this.isDownloaded = true
        })
    }

    setResizingHTMLElement(resizingHTMLElement: HTMLElement | undefined) {
        if (resizingHTMLElement)
            this._resizingHTMLElement = resizingHTMLElement
    }

    updatePositionOfGroupsByResizingHTMLElement() {
        if (this._resizingHTMLElement) {
            const resizingHtmlElementBoundingRect = this._resizingHTMLElement.getBoundingClientRect()

            const x = resizingHtmlElementBoundingRect.x * GLOBAL_PX_SIZE / 1000,
                y = -(resizingHtmlElementBoundingRect.y * GLOBAL_PX_SIZE / 1000),
                w = resizingHtmlElementBoundingRect.width * GLOBAL_PX_SIZE / 1000,
                h = resizingHtmlElementBoundingRect.height * GLOBAL_PX_SIZE / 1000

            this.centerGroup.position.set(x + w / 2, y - (h / 2), 0)

            //initialize update method
            if (this.isDownloaded) this.update(x, y, w, h)
        }
    }

    protected abstract loadRes(): Promise<any>

    protected abstract start(): void

    protected abstract update(x: number, y: number, w: number, h: number): void

    public onDownloaded(): void {
    }
}

class NotLoadedThreeDCase extends ThreeDCase {
    constructor(resizingHTMLElement: HTMLElement) {
        super(resizingHTMLElement);
    }

    protected start() {
    }

    protected update() {
    }

    protected loadRes(): Promise<any> {
        return Promise.resolve(undefined);
    }
}

class MyCase extends ThreeDCase {

    private center_texture: THREE.Texture | undefined
    private center_model: THREE.Group | undefined

    private left_top_texture: THREE.Texture | undefined
    private left_top_model: THREE.Group | undefined

    private right_top_texture: THREE.Texture | undefined
    private right_top_model: THREE.Group | undefined

    private left_bottom_texture: THREE.Texture | undefined
    private left_bottom_model: THREE.Group | undefined

    private right_bottom_texture: THREE.Texture | undefined
    private right_bottom_model: THREE.Group | undefined

    private glsl = {
        vertexShader: `
            varying vec2 vUv;
            void main() {
                vUv = uv;
                gl_Position = projectionMatrix * modelViewMatrix * vec4(position, 1.0);
            }
        `,
        fragmentShader: `
            uniform sampler2D uTexture;
            varying vec2 vUv;
            void main() {
                gl_FragColor = texture2D(uTexture, vUv);
            }
        `
    }

    protected async loadRes(): Promise<any> {
        const objLoader = new OBJLoader()
        const textureLoader = new TextureLoader()

        this.center_model = await objLoader.loadAsync(centerObj)
        this.center_texture = await textureLoader.loadAsync(centerTexture)

        this.left_top_model = await objLoader.loadAsync(leftTopObj)
        this.left_top_texture = await textureLoader.loadAsync(leftTopTexture)

        this.right_top_model = await objLoader.loadAsync(rightTopObj)
        this.right_top_texture = await textureLoader.loadAsync(rightTopTexture)

        this.right_bottom_model = await objLoader.loadAsync(rightBottomObj)
        this.right_bottom_texture = await textureLoader.loadAsync(rightBottomTexture)

        this.left_bottom_model = await objLoader.loadAsync(leftBottomObj)
        this.left_bottom_texture = await textureLoader.loadAsync(leftBottomTexture)
    }

    constructor(resizingHTMLElement: HTMLElement) {
        super(resizingHTMLElement);
    }

    protected start(): void {
        if (this.center_model) {
            this.center_model.scale.set(1.7, 1.7, 1.7)
            let center = this.center_model.children[0] as THREE.Mesh
            center.material = new ShaderMaterial({
                uniforms: {
                    uTexture: {
                        value: this.center_texture
                    }
                },
                ...this.glsl
            })

            this.centerGroup.add(this.center_model)
        }
    }

    protected update(): void {
    }
}

export default class ThreeD {

    private _time: number = 0

    private _canvas: HTMLCanvasElement | undefined

    private static _listOfNameOfThreeDCases: Array<{ id: string, type: any }> = [
        {id: "myCase", type: MyCase},
        {id: "myCase2", type: MyCase},
        {id: "myCase3", type: MyCase},
        {id: "myCase4", type: MyCase}
    ]
    private static _listOfLoadedThreeDCases: Array<{ id: string, exemplar: ThreeDCase }> = []

    private _renderer: THREE.WebGLRenderer | undefined
    private _camera: THREE.PerspectiveCamera = new THREE.PerspectiveCamera(50)
    private _scene: THREE.Scene = new THREE.Scene()

    private _composer: EffectComposer | undefined
    private _shaderPass = new ShaderPass({
        uniforms: {
            tDiffuse: {value: null},
            uTime: {value: 0.0}
        },
        vertexShader: `
                varying vec2 vUv;
                void main() {
                    vUv = uv; 
                    gl_Position = projectionMatrix * modelViewMatrix * vec4( position, 1.0 );
                }
            `,
        fragmentShader: `
                uniform sampler2D tDiffuse;
                uniform float uTime;
                varying vec2 vUv;
                
                void main() {
                    gl_FragColor = vec4(
                        texture2D( tDiffuse, vUv).r, 
                        texture2D( tDiffuse, vUv).g, 
                        texture2D( tDiffuse, vUv).b, 
                        texture2D( tDiffuse, vUv).a
                    );
                }
            `
    })

    constructor() {

        //time initialize
        setInterval(() => this._time += 0.001, 1)
    }

    start(canvas: HTMLCanvasElement) {
        this._canvas = canvas
        this._renderer = new THREE.WebGLRenderer({alpha: true, canvas: this._canvas})

        //start rendering
        this._composer = new EffectComposer(this._renderer)
        this._composer.addPass(new RenderPass(this._scene, this._camera))
        this._composer.addPass(this._shaderPass)
        this.render(true)

        //start resizing
        this._resize()
        window.addEventListener("resize", () => this._resize())

        //render on scroll
        window.addEventListener("scroll", () => this.render())
    }

    private _resize() {
        if (this._canvas && this._renderer && this._composer) {
            this._renderer.setSize(window.innerWidth, window.innerHeight)
            this._renderer.setPixelRatio(window.devicePixelRatio)
            this._composer.setSize(window.innerWidth, window.innerHeight)
            this._composer.setPixelRatio(window.devicePixelRatio)
            this._camera.aspect = window.innerWidth / window.innerHeight
            this._camera.updateProjectionMatrix()
        }
        console.log(ThreeD._listOfLoadedThreeDCases)
    }

    render(useRequestAnimationFrame: boolean = false) {

        //Camera positing
        const angle = (50 * Math.PI / 180)
        this._camera.position.set((window.innerWidth * GLOBAL_PX_SIZE / 2 / 1000), -(window.innerHeight * GLOBAL_PX_SIZE / 2 / 1000), ((window.innerHeight * GLOBAL_PX_SIZE / 2) / Math.tan(angle / 2)) / 1000)
        this._camera.lookAt((window.innerWidth * GLOBAL_PX_SIZE / 2 / 1000), -(window.innerHeight * GLOBAL_PX_SIZE / 2 / 1000), 0)

        //Rendering
        if (this._composer) {
            this._composer.render()
            this._shaderPass.material.uniforms.uTime.value = this._time

            if (useRequestAnimationFrame)
                window.requestAnimationFrame(() => this.render(true))
        }

        //updatingCases
        ThreeD._listOfLoadedThreeDCases.forEach(elem => elem.exemplar.updatePositionOfGroupsByResizingHTMLElement())
    }


    //adding and removing three d cases from scene

    addThreeDCase(id: string, resizingHTMLElement: HTMLElement) {

        /* Здесь мы сверяем, есть ли уже в списке _listOfLoadedThreeDCases, где хранятся все id возможных кейсов и их
        конструкторы, созданный экземпляр кейса с таким же id */
        const searchedThreeDCaseInListOfLoadedThreeDCases =
            ThreeD._listOfLoadedThreeDCases
                .find((LoadedThreeDCase) => LoadedThreeDCase.id === id)

        //И если он есть мы не создавая нового меняем в существующем ResizingHTMLElement
        if (searchedThreeDCaseInListOfLoadedThreeDCases) {
            searchedThreeDCaseInListOfLoadedThreeDCases.exemplar.setResizingHTMLElement(resizingHTMLElement)
            this._scene.add(searchedThreeDCaseInListOfLoadedThreeDCases.exemplar.mainGroup)
        } else {
            const NameOfThreeDCase = ThreeD._listOfNameOfThreeDCases.find(NameOfThreeDCase => NameOfThreeDCase.type)
            if (NameOfThreeDCase) {
                const exemplar = new NameOfThreeDCase.type(resizingHTMLElement)
                exemplar.onDownloaded = () => {
                    ThreeD._listOfDownloadedThreeDCases.push(id)
                    ThreeD.startReactionOnChangeOfDownloadedThreeDCasesList()
                }
                this._scene.add(exemplar.mainGroup)

                ThreeD._listOfLoadedThreeDCases.push({
                    id: id,
                    exemplar: exemplar
                })
            } else {
                ThreeD._listOfLoadedThreeDCases.push({
                    id: id,
                    exemplar: new NotLoadedThreeDCase(resizingHTMLElement)
                })
            }
        }
    }

    removeThreeDCase(id: string) {

        /* Здесь мы сверяем, есть ли уже в списке _listOfLoadedThreeDCases, где хранятся все id возможных кейсов и их
        конструкторы, созданный экземпляр кейса с таким же id */
        const searchedThreeDCaseInListOfLoadedThreeDCases =
            ThreeD._listOfLoadedThreeDCases
                .find((LoadedThreeDCase) => LoadedThreeDCase.id === id)

        if (searchedThreeDCaseInListOfLoadedThreeDCases) {
            this._scene.remove(searchedThreeDCaseInListOfLoadedThreeDCases.exemplar.mainGroup)
        }
    }


    //Здесь добавляю возможность отследить загрузку кейсов

    private static listOfListenersOfDownloadedThreeDCases: Array<{ func: () => void, typeOfCase: string, started: boolean }> = []
    private static _listOfDownloadedThreeDCases: Array<string> = []

    private static startReactionOnChangeOfDownloadedThreeDCasesList() {
        this.listOfListenersOfDownloadedThreeDCases.forEach(elem => {

            if (!elem.started && ThreeD._listOfDownloadedThreeDCases.includes(elem.typeOfCase)) {
                elem.func()
                elem.started = true
            }
        })
    }

    static checkDownloadedThreeDCasesList(typeOfCase: string) {
        const index = ThreeD._listOfDownloadedThreeDCases.findIndex(elem => elem === typeOfCase)
        return index !== -1
    }

    static addListenerDownloadedThreeDCasesList(func: () => void, typeOfCase: string) {
        if (-1 === ThreeD.listOfListenersOfDownloadedThreeDCases.findIndex(elem => elem.typeOfCase === typeOfCase && elem.func == func))
            ThreeD.listOfListenersOfDownloadedThreeDCases.push({func, typeOfCase, started: false})
    }

    static removeListenerDownloadedThreeDCasesList(func: () => void, typeOfCase: string) {
        const index = ThreeD.listOfListenersOfDownloadedThreeDCases.findIndex(elem => elem.typeOfCase === typeOfCase && elem.func == func)
        if (index !== -1) {
            ThreeD.listOfListenersOfDownloadedThreeDCases.splice(index)
        }
    }
}