import {MutableRefObject, useEffect, useRef, useState} from "react";
import anime, {AnimeInstance} from "animejs";

export default function useHorizontalScroll(containerRef: MutableRefObject<HTMLDivElement | null>) {
    const directionRef = useRef<number>(0)
    const animationRef = useRef<AnimeInstance>()
    const eventListenerRef = useRef<any>()
    const timeOutRef = useRef<ReturnType<typeof setTimeout>>()

    const [isScrollVisible, setIsScrollVisible] = useState<boolean>(false)

    useEffect(() => {
        if (containerRef.current) {
            eventListenerRef.current = (event:any) => {
                event.preventDefault()

                if (animationRef.current) animationRef.current.pause()

                if (containerRef.current) {
                    animationRef.current = anime({
                        targets: [containerRef.current],
                        scrollLeft: [containerRef.current.scrollLeft, containerRef.current.scrollLeft + event.deltaY * 3],
                        easing: "easeOutCubic",
                        duration: 500,
                        autoplay: true
                    })

                    clearTimeout(timeOutRef.current)

                    if(!isScrollVisible) {
                        setIsScrollVisible(true)
                        timeOutRef.current = setTimeout(() => {
                            setIsScrollVisible(false)
                        }, 1000)
                    }
                }
            }

            containerRef.current.addEventListener("wheel", eventListenerRef.current)
        }

        return () => {
            if (containerRef.current) containerRef.current.removeEventListener("wheel", eventListenerRef.current)
        }
    }, [])

    return isScrollVisible
}