import React, {HTMLAttributes, MouseEventHandler, useRef} from "react";
import "./Button.sass"
import {useTagsList} from "../Tag/Tag";
import {IAnimated, useJustAnimation} from "../Animation/Animation";

interface IButtonProps extends HTMLAttributes<HTMLButtonElement> {
    size?: "small" | "medium" | "big"
    hasText?: boolean
    children?: string
    leftIconSrc?: string
    rightIconSrc?: string
    className?: string
    onClick?: MouseEventHandler
    color?: string
    textColor?: string
    style?: React.CSSProperties,
    delay?: number
}

const Button: React.FC<IButtonProps & IAnimated> =
    ({
         children,
         size = "medium",
         hasText = true,
         leftIconSrc = "",
         rightIconSrc = "",
         className,
         onClick,
         color,
         textColor,
         style,
         animationOrder,
         animationParams,
         doStartAnimation,
         deps = [],
        delay,
         ...props
     }) => {

        //Animation
        const containerRef = useRef<HTMLButtonElement>(null)
        const justAnimationStyles = useJustAnimation({
            doStartAnimation,
            objectRef: containerRef,
            animationOrder: delay,
            animationParams,
            deps
        })

        return children || leftIconSrc || rightIconSrc ?
            <button
                ref={containerRef}
                onClick={onClick}
                style={{...style, color: textColor, backgroundColor: color, ...justAnimationStyles}}
                className={[
                    "button",
                    className,
                    children ? null : "button_only-icon",
                    size === "small" ? "button_small" : null,
                    color ? "button_filled" : null,
                    size === "big" ? "button_big" : null
                ].join(" ")} {...props}
            >
                {
                    leftIconSrc
                        ?
                        <span
                            className="button__icon button__icon_at-left"
                            style={{backgroundImage: `url('${leftIconSrc}')`}}
                        />
                        :
                        null
                }
                {children}
                {
                    rightIconSrc
                        ?
                        <span
                            className="button__icon button__icon_at-right"
                            style={{backgroundImage: `url('${rightIconSrc}')`}}
                        />
                        :
                        null
                }
            </button> : null
    }

export default Button;